import '../assets/css/Intro.css'
import portrait from '../assets/images/portrait.png'

export const Intro = () => {
	return (
		<div className="Intro" id="accueil">
			<img src={portrait} alt="Portrait Icham Duret" />
			<p>
				<span className="title">
					Bonjour ! Je suis <span className="name">Icham</span>,
					dveloppeur front-end
				</span>
				<br />
				Diplm d'OpenClassrooms en 2024, habitu du framework React, je
				maitrise aussi bien le JavaScript que que le Sass ou
				l'architecture Flux. En plus de mes projets Openclassrooms, j'ai
				dj ralis des projets scolaires ou personnels en PHP,
				Node.js, MySQL ou encore Java8
			</p>
		</div>
	)
}
